.navContainer {
  position: fixed;
  color: #eee;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 1.6rem;
  display: flex;
  width: 100%;
  height: 6rem;
  align-items: center;
  background-color: white;
  z-index: 1000;
  justify-content: space-between;
  transition: color 0.2s, background-color 0.2s;
}

.navContainer .navbar-brand {
  margin-left: 3rem;
  color: #2f72ff;
  transition: color 0.2s, background-color 0.2s;
  display: flex;
  align-items: center;
}

@media (max-width: 75em) {
  .navContainer .navbar-brand {
    margin-left: 2rem;
  }
}

@media (max-width: 68.75em) {
  .navContainer .navbar-brand {
    margin-left: 1.5rem;
  }
}

.navContainer .navbar-brand h3 {
  font-size: 3rem;
}

.navContainer .navbar-brand .navbar__icon {
  width: 7rem;
  height: 7rem;
  display: inline;
  margin-right: 1rem;
}

.navContainer .auth-links {
  color: #eee;
  display: flex;
  align-self: stretch;
  align-items: center;
  transition: color 0.2s, background-color 0.2s;
}

.navContainer .auth-links div {
  display: inline-block;
}

.navContainer .auth-links .link-list {
  min-height: 100%;
  list-style-type: none;
  background-color: transparent !important;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: stretch;
  color: white;
  transition: color 0.2s, background-color 0.2s;
}

.navContainer .auth-links .link-list .active {
  background: none !important;
}

.navContainer .auth-links .link-list a:active,
.navContainer .auth-links .link-list a:link {
  font-size: 1.5rem;
  /*
                @media (max-width: $bp-largest) {
                    font-size: 2.5rem;
                }
                @media (max-width: $bp-large) {
                    font-size: 2rem;
                }
                @media (max-width: 68.75em) {
                    font-size: 1.5rem;
                }
                */
  color: #2f72ff;
  background-color: none !important;
  transition: color 0.2s, background-color 0.2s;
  text-decoration: none;
}

.navContainer .auth-links .link-list label {
  color: #2f72ff;
  transition: color 0.2s, background-color 0.2s;
  font-size: 2rem;
  /*
                @media (max-width: $bp-largest) {
                    //font-size: 2.5rem;
                }
                @media (max-width: $bp-large) {
                    //font-size: 2rem;
                }
                @media (max-width: 68.75em) {
                    //font-size: 1.5rem;
                }
                */
}

.navContainer .auth-links .link-list li {
  color: #2f72ff;
  margin: 0 !important;
  text-align: center;
  padding: 0;
  float: left;
  list-style-type: none;
  display: flex;
