.navCheckbox {
  display: none;
}

.navCheckbox:checked ~ .hack-modal .animation-wrapper {
  -webkit-animation: scaleTranslateModal 1s;
          animation: scaleTranslateModal 1s;
}

.animation-wrapper {
  z-index: 999999999999999999999 !important;
}

@-webkit-keyframes scaleTranslateModal {
  0% {
    top: 50%;
    left: 50%;
    transform: scale(0.125) translate(-50%, -50%);
  }
  100% {
    top: 50%;
    left: 50%;
    transform: scale(1) translate(-50%, -50%);
  }
}

@keyframes scaleTranslateModal {
  0% {
    top: 50%;
    left: 50%;
    transform: scale(0.125) translate(-50%, -50%);
  }
  100% {
    top: 50%;
    left: 50%;
    transform: scale(1) translate(-50%, -50%);
  }
}

.modal-main {
