@-webkit-keyframes moveInUp {
    0% {
        transform: translateX(-5em);
        opacity: 0;
    }
    80% {
        transform: translateX(.5em);
    }
    100% {
        transform: translateX(0px);
        opacity: 1;
    }
}

@keyframes moveInUp {
    0% {
        transform: translateX(-5em);
        opacity: 0;
    }
    80% {
        transform: translateX(.5em);
    }
    100% {
        transform: translateX(0px);
        opacity: 1;
    }
}

@media (min-width: 40em) {
    .block1,
    .block2,
    .block3,
    .block4 {
        padding: 2em;
    }
    .cover-container {
        margin-top: 10rem;
    }
    .landingLogin__contents {
        margin-top: 10rem;
    }
}

@media (min-width: 50em) {
    .block1,
    .block2,
    .block3,
    .block4 {
        padding: 4em;
    }
}

.cover-container {
    -webkit-animation: moveInUp 1s ease-out;
            animation: moveInUp 1s ease-out;
}

.landingLogin__contents {
    -webkit-animation: moveInUp 1s ease-out;
            animation: moveInUp 1s ease-out;
}


